<template>
    <div>
        <!--  -->
        <el-breadcrumb :separator-icon="ArrowRight">
            <el-breadcrumb-item :to="{ path: '/home/welcome' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>

        <!--  -->
        <el-card>
            <div id="main" style="width:750px;height:400px"></div>
        </el-card>
    </div>
</template>

<script>
import { ArrowRight, Search, EditPen, Location, Edit } from '@element-plus/icons-vue'
import { merge } from 'lodash'
//  echarts
import * as echarts from 'echarts'
export default {
    data() {
        return {
            ArrowRight, Search, EditPen, Location, Edit,
            // 
            options: {
                title: {
                    text: ''
                },
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'cross',
                        label: {
                            backgroundColor: '#E9EEF3'
                        }
                    }
                },
                grid: {
                    left: '3%',
                    right: '4%',
                    bottom: '3%',
                    containLabel: true
                },
                xAxis: [
                    {
                        boundaryGap: false
                    }
                ],
                yAxis: [
                    {
                        type: 'value'
                    }
                ]
            },
        }
    },
    created() {

    },
    // dom
    mounted() {
        // 
        var myChart = echarts.init(document.getElementById('main'));
        this.$api.httpGet('reports/type/1', { showOuter: 'error', error: [''] })
            .then(res => {
                const result = merge(this.options, res.data);
                myChart.setOption(result);
            })
    }
}
</script>

<style lang='less' scoped></style>