<template>
    <div>
        <!--  -->
        <el-breadcrumb :separator-icon="ArrowRight">
            <el-breadcrumb-item :to="{ path: '/home/welcome' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <el-row gutter="20">
                <el-col :span="10">
                    <el-input v-model="queryInfo.query" placeholder="" clearable @clear="getOrderList"
                        @keyup.enter="getOrderList">
                        <template #append>
                            <el-button :icon="Search" @click="getOrderList" />
                        </template>
                    </el-input>
                </el-col>
            </el-row>
            <!--  -->
            <el-table :data="orderList" border stripe fit>
                <el-table-column type="index" label="#" width="40px"></el-table-column>
                <el-table-column label="" prop="order_number" min-width="180px"></el-table-column>
                <el-table-column label="" prop="order_price"></el-table-column>
                <el-table-column label="" width="143px">
                    <template #default="scope">
                        {{ dateFormat(scope.row.create_time) }}
                    </template>
                </el-table-column>
                <el-table-column label="" prop="pay_status">
                    <template #default="scope">
                        <el-tag type="success" v-if="scope.row.pay_status === '1'"></el-tag>
                        <el-tag type="danger" v-if="scope.row.pay_status === '0'"></el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="" prop="is_send" width="73px"></el-table-column>
                <el-table-column label="" width="138px">
                    <el-button type="success" size="small" :icon="Location" @click="showProgressBox"></el-button>
                </el-table-column>
                <el-table-column label="" width="138px">
                    <el-button type="primary" size="small" :icon="EditPen" @click="showBox"></el-button>
                </el-table-column>
                <el-table-column label="" width="215px">
                    <template #default="scope">
                        <el-button type="warning" size="small" :icon="Search"
                            @click="showOrderDetail(scope.row.order_id)"></el-button>
                        <el-button type="danger" size="small" :icon="Edit"
                            @click="showEditOrder(scope.row.order_id)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-pagination v-model:current-page="queryInfo.pagenum" v-model:page-size="queryInfo.pagesize"
                :page-sizes="[5, 10, 20, 30]" small background :total="total"
                layout="total, sizes, prev, pager, next, jumper" @size-change="handleSizeChange"
                @current-change="handleCurrentChange" />
        </el-card>
        <!--  -->
        <el-dialog v-model="addressDialogVisible" title="" width="80%" @close="addressDialogClosed">
            <el-form :model="addressForm" ref="addressFormRef" :rules="addressFormRules" label-width="100px">
                <el-form-item label="/" prop="address1">
                    <el-cascader :options="cityData" v-model="addressForm.address1" :props="cascadeProps"
                        style="width: 100%;"></el-cascader>
                </el-form-item>
                <el-form-item label="" prop="address2">
                    <el-input v-model="addressForm.address2" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <template #footer>
                <span class="dialog-footer">
                    <el-button @click="addressDialogVisible = false"></el-button>
                    <el-button type="primary" @click="addressDialogVisible = false"></el-button>
                </span>
            </template>
        </el-dialog>
        <!--  -->
        <el-dialog v-model="progressDialogVisible" title="" width="50%">
            <el-timeline>
                <el-timeline-item v-for="(activity, index) in progressInfo" :key="index" :timestamp="activity.time">
                    {{ activity.context }}
                </el-timeline-item>
            </el-timeline>
        </el-dialog>
        <!--     -->
        <el-dialog v-model="orderDialogVisible" title="" width="50%" @close="orderDialogClosed">
            <el-form :model="orderForm" ref="orderFormRef" :rules="orderFormRules" label-width="80px" label-position="left">
                <el-form-item label="">
                    <el-input v-model="orderForm.order_number" disabled />
                </el-form-item>
                <el-form-item label="" prop="order_price">
                    <el-input v-model="orderForm.order_price" :disabled="disable" />
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="orderForm.create_time" disabled />
                </el-form-item>
                <el-form-item label="" prop="order_pay">
                    <el-select v-model="orderForm.order_pay" :disabled="disable">
                        <el-option v-for="item in orderPayList" :key="item.value" :label="item.label" :value="item.value" />
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="pay_status">
                    <el-select v-model="orderForm.pay_status" :disabled="disable">
                        <el-option v-for="item in payStatusList" :key="item.value" :label="item.label"
                            :value="item.value" />
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="is_send">
                    <el-select v-model="orderForm.is_send" :disabled="disable">
                        <el-option v-for="item in isSendList" :key="item.value" :label="item.label" :value="item.value" />
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" rows="3" v-model="orderForm.consignee_addr" disabled />
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="orderForm.order_fapiao_title" disabled />
                </el-form-item>
                <el-form-item label="-">
                    <el-input v-model="orderForm.order_fapiao_company" disabled />
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" rows="2" v-model="orderForm.order_fapiao_content" disabled />
                </el-form-item>
            </el-form>
            <!--  -->
            <el-table v-if="disable" :data="orderForm.goods" style="width: 100%" border stripe fit>
                <el-table-column type="index" label="#" />
                <el-table-column prop="goods_name" label=""/>
                <el-table-column prop="goods_price" label="" width="76"/>
                <el-table-column prop="goods_number" label="" width="76"/>
                <el-table-column prop="goods_total_price" label="" width="60"/>
            </el-table>
            <template #footer>
                <span class="dialog-footer">
                    <el-button v-if="disable" type="danger" @click="triggerToEdit"></el-button>
                    <el-button @click="orderDialogVisible = false"></el-button>
                    <el-button v-if="disable" type="primary" @click="orderDialogVisible = false"></el-button>
                    <el-button v-else type="primary" @click="editOrder"></el-button>
                </span>
            </template>
        </el-dialog>
    </div>
</template>

<script>
import { ArrowRight, Search, EditPen, Location, Edit } from '@element-plus/icons-vue'
import cityData from './citydata.js'
export default {
    data() {
        const priceValidate = (rule, value, callback) => {
            if (value < 0) {
                callback(new Error('0'));
            }
            callback();
        }
        return {
            ArrowRight, Search, EditPen, Location, Edit,
            cityData,
            // 
            queryInfo: {
                query: '',
                pagenum: 1,
                pagesize: 5,
            },
            // 
            total: 0,
            // 
            orderList: [],
            // 
            addressDialogVisible: false,
            // 
            addressForm: {
                address1: [],
                address2: ''
            },
            // 
            addressFormRules: {
                address1: [
                    { required: true, message: '/', trigger: 'blur' },
                ],
                address2: [
                    { required: true, message: '', trigger: 'blur' },
                ]
            },
            // 
            cascadeProps: {
                expandTrigger: 'hover'
            },
            // 
            progressDialogVisible: false,
            // 
            progressInfo: [],
            // 
            orderDialogVisible: false,
            // 
            orderForm: {},
            // 
            orderFormRules: {
                order_price: [
                    { required: true, message: '', trigger: 'blur' },
                    { validator: priceValidate, trigger: 'blur' }
                ],
                order_pay: [
                    { required: true, message: '', trigger: 'blur' },
                ],
                pay_status: [
                    { required: true, message: '', trigger: 'blur' },
                ],
                is_send: [
                    { required: true, message: '', trigger: 'blur' },
                ],
            },
            // 
            disable: false,
            // 
            orderPayList: [
                { label: '', value: '0' },
                { label: '', value: '1' },
                { label: '', value: '2' },
                { label: '', value: '3' },
            ],
            // 
            isSendList: [
                { label: '', value: '' },
                { label: '', value: '' },
            ],
            // 
            payStatusList: [
                { label: '', value: '0' },
                { label: '', value: '1' },
            ],
            // 
            editInfo: {
                is_send: '',
                order_pay: '0',
                order_price: 0,
                pay_status: '0'
            }
        }
    },
    created() {
        this.getOrderList();
    },
    methods: {
        // 
        getOrderList() {
            this.$api.httpGet('orders', { showOuter: 'error', error: [''] }, 200, { params: this.queryInfo })
                .then(res => {
                    this.total = res.data.total;
                    this.orderList = res.data.goods;
                })
        },
        //  -> yyyy-mm-dd hh:mm:ss
        dateFormat(timestamp) {
            const date = new Date(timestamp);

            const y = date.getFullYear();
            const m = (date.getMonth() + 1 + '').padStart(2, '0');
            const d = (date.getDate() + '').padStart(2, '0');

            const hh = (date.getHours() + '').padStart(2, '0');
            const mm = (date.getMinutes() + '').padStart(2, '0');
            const ss = (date.getSeconds() + '').padStart(2, '0');

            return `${y}-${m}-${d} ${hh}:${mm}:${ss}`;
        },
        // 
        handleSizeChange(newSize) {
            this.queryInfo.pagesize = newSize;
            this.getOrderList();
        },
        // 
        handleCurrentChange(newPage) {
            this.queryInfo.pagenum = newPage;
            this.getOrderList();
        },
        // 
        showBox() {
            this.addressDialogVisible = true;
        },
        // 
        addressDialogClosed() {
            this.$refs.addressFormRef.resetFields();
        },
        // 
        showProgressBox() {
            // 
            // this.$api.httpGet('/kuaidi/804909574412544580', {showOuter: 'error', error: ['']})
            // .then(res => {
            //     this.progressInfo = res.data;
            //     console.log(this.progressInfo);
            // })
            const res = {
                "data": [
                    {
                        "time": "2018-05-10 09:39:00",
                        "ftime": "2018-05-10 09:39:00",
                        "context": ",,",
                        "location": ""
                    },
                    {
                        "time": "2018-05-10 08:23:00",
                        "ftime": "2018-05-10 08:23:00",
                        "context": "[]  95338",
                        "location": ""
                    },
                    {
                        "time": "2018-05-10 07:32:00",
                        "ftime": "2018-05-10 07:32:00",
                        "context": " []",
                        "location": ""
                    },
                    {
                        "time": "2018-05-10 02:03:00",
                        "ftime": "2018-05-10 02:03:00",
                        "context": "[], []",
                        "location": ""
                    },
                    {
                        "time": "2018-05-09 23:05:00",
                        "ftime": "2018-05-09 23:05:00",
                        "context": " []",
                        "location": ""
                    },
                    {
                        "time": "2018-05-09 21:21:00",
                        "ftime": "2018-05-09 21:21:00",
                        "context": "[], []",
                        "location": ""
                    },
                    {
                        "time": "2018-05-09 13:07:00",
                        "ftime": "2018-05-09 13:07:00",
                        "context": " ",
                        "location": ""
                    },
                    {
                        "time": "2018-05-09 12:25:03",
                        "ftime": "2018-05-09 12:25:03",
                        "context": "",
                        "location": ""
                    },
                    {
                        "time": "2018-05-09 12:22:24",
                        "ftime": "2018-05-09 12:22:24",
                        "context": "HLA",
                        "location": ""
                    },
                    {
                        "time": "2018-05-08 21:36:04",
                        "ftime": "2018-05-08 21:36:04",
                        "context": "",
                        "location": ""
                    }
                ],
                "meta": { "status": 200, "message": "" }
            }
            this.progressInfo = res.data;
            this.progressDialogVisible = true;
        },
        // 
        async showOrderDetail(id) {
            // 
            await this.getOrderInfo(id);
            // 
            this.orderForm.goods.forEach((item, index) => {
                this.getGoodInfoById(item.goods_id, index);
            })
            this.disable = true;
            this.orderDialogVisible = true;
        },
        // 
        async getOrderInfo(id) {
            await this.$api.httpGet('orders/' + id, { showOuter: 'error', error: [''] })
                .then(res => {
                    this.orderForm = res.data;
                })
        },
        // 
        orderDialogClosed() {
            this.$refs.orderFormRef.resetFields();
        },
        // 
        showEditOrder(id) {
            this.getOrderInfo(id);
            this.disable = false;
            this.orderDialogVisible = true;
        },
        // 
        editOrder() {
            for (let key in this.editInfo) {
                this.editInfo[key] = this.orderForm[key];
            }
            this.$api.httpPut('orders/' + this.orderForm.order_id, this.editInfo, { showOuter: 'error', success: [''], error: [''] }, 201)
                .then(() => {
                    this.orderDialogVisible = false;
                    this.getOrderList();
                })
        },
        // 
        triggerToEdit() {
            this.disable = false;
        },
        // id
        getGoodInfoById(id, index) {
            this.$api.httpGet(`goods/${id}`,{showOuter: 'error', error: ['']})
            .then(res => {
                this.orderForm.goods[index].goods_name = res.data.goods_name;
            })
        },
    }
}
</script>

<style lang='less' scoped></style>